export default {
	data() {
		return {
			form: {
				amount: uni.getStorageSync("userInfo").balance || 0,
				name: "",
				back: "",
				card: "",
				tel: ""
			}
		}
	},
	onLoad() {

	},
	methods: {
		showMoney() {
			let _this = this;
			if (_this.form.amount < 0) {
				return uni.showToast({
					title: "提现金额需为大于0",
					icon: 'none'
				});
			}
			if (_this.form.amount > uni.getStorageSync("userInfo").balance) {
				return uni.showToast({
					title: "提现金额不能大于余额",
					icon: 'none'
				});
			}
			if (_this.form.name === '') {
				return uni.showToast({
					title: "请输入账户名",
					icon: 'none'
				});
			}
			if (_this.form.back === '') {
				return uni.showToast({
					title: "请输入开户行",
					icon: 'none'
				});
			}
			if (_this.form.card === '') {
				return uni.showToast({
					title: "请输入银行卡号",
					icon: 'none'
				});
			}
			if (!/^[1][0-9][0-9]{9}$/.test(_this.form.tel)) {
				return uni.showToast({
					title: "请输入正确的预留人手机",
					icon: 'none'
				});
			}
			_this.request({
				url: _this.http.addWithdrawal,
				data: {
					driverId: uni.getStorageSync("userInfo").driverId,
					withdrawalMoney: _this.form.amount,
					accountUser: _this.form.name,
					openingBank: _this.form.back,
					bankCard: _this.form.card,
					mobile: _this.form.tel
				},
				success: (res) => {
					if (res.code === '0') {
						uni.showToast({
							title: "提现成功",
							icon: 'success'
						});
					} else {
						uni.showToast({
							title: res.message,
							icon: 'none'
						});
					}
				}
			});
		}
	}
}
